import { Component, OnInit,ViewChild } from '@angular/core';
import { first } from 'rxjs/operators';
import { BsModalService, BsModalRef } from "ngx-bootstrap/modal";
import { HotelService } from '@app/_services/hotel.service';
import { AgGridAngular } from 'ag-grid-angular';
import { AddNewHotelComponent } from './add-new-hotel/add-new-hotel.component';
import { EditHotelComponent } from './edit-hotel/edit-hotel.component';
import { DeleteHotelComponent } from './delete-hotel/delete-hotel.component';
import { ButtonRendererComponent } from './renderer/button-renderer.component';
@Component({ templateUrl: 'list.component.html' })
export class ListComponent implements OnInit {
    @ViewChild('agGrid') agGrid: AgGridAngular;
    bsModalRef: BsModalRef;
    frameworkComponents: any;
    rowDataClicked1 = {};
    hotels = null;
    columnDefs = [
        {headerName: 'Hotel Name', field: 'HotelName',sortable: true, filter: true 
  
    },
        {headerName: 'City ID', field: 'CityId',sortable: true, filter: true },
        {headerName: 'Address', field: 'Adddress',sortable: true, filter: true},
        {
          headerName: 'Button Col 1',
          cellRenderer: 'buttonRenderer',
          cellRendererParams: {
            onClick: this.onBtnClick1.bind(this),
            label: 'Click 1'
          }
        }
        //  {headerName: '', field: '',  cellRenderer:'<span class="btn btn-primary btn-xs" (click)="editPost(1)"><span class="glyphicon glyphicon-pencil"></span></span>'
        // +'<span class="btn btn-danger btn-xs" (click)="deletePost(item.postId, item.title)"> <span class="glyphicon glyphicon-trash"></span></span>'
        // }
    ];  

   
    constructor(private hotelService: HotelService,private bsModalService: BsModalService) {

      this.frameworkComponents = {
        buttonRenderer: ButtonRendererComponent,
      }
    }
    onBtnClick1(e) {
      this.rowDataClicked1 = e.rowData;
    }

    ngOnInit() {
    
        this.hotelService.getAll()
            .pipe(first())
            .subscribe(hotels => this.hotels = hotels);
        
    }
    getSelectedRows() {
        const selectedNodes = this.agGrid.api.getSelectedNodes();
        const selectedData = selectedNodes.map( node => node.data );
        const selectedDataStringPresentation = selectedData.map( node => node.HotelName + ' ' + node.Adddress).join(', ');
        alert(`Selected nodes: ${selectedDataStringPresentation}`);
    }
    getHotels() {
        this.hotelService.getAll().subscribe(data => {
          Object.assign(this.hotels, data);
        }, error => {
          console.log("Error while getting posts ", error);
        });
      }
    addNewHotel() {
        this.bsModalRef = this.bsModalService.show(AddNewHotelComponent);
        this.bsModalRef.content.event.subscribe(result => {
          if (result == 'OK') {
            this.getHotels();
          }
        });
      }
      deleteHotel(HotelId: number, HotelName: string) {
        this.bsModalRef = this.bsModalService.show(DeleteHotelComponent);
        this.bsModalRef.content.HotelId = HotelId;
        this.bsModalRef.content.HotelName = HotelName;
        this.bsModalRef.content.event.subscribe(result => {
          console.log("deleted", result);
          if (result == 'OK') {
            setTimeout(() => {
              this.hotels=[];
              this.getHotels();
            }, 5000);
          }
        });
      }
      editHotel(HotelId: number) {
        //this.hotelService.update(HotelId);
    
        this.bsModalRef = this.bsModalService.show(EditHotelComponent);
        this.bsModalRef.content.event.subscribe(result => {
          if (result == 'OK') {
            setTimeout(() => {
              this.getHotels();
            }, 5000);
          }
        });
      }
    // deleteUser(id: string) {
    //     const user = this.users.find(x => x.id === id);
    //     user.isDeleting = true;
    //     this.accountService.delete(id)
    //         .pipe(first())
    //         .subscribe(() => {
    //             this.users = this.users.filter(x => x.id !== id) 
    //         });
    // }
}